.home-container {
    width: 100%;
    // height: 2500px;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    position: relative;
    align-items: center;
    flex-direction: column;
    .bg-img {
        object-fit: cover;
        width: 103%;
        height: 102vh;
        filter: grayscale(50%) blur(2px) contrast(120%);
        margin-top: -10px;
        object-position: top;
        position: fixed;
    }
}
.shape-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    justify-content: center;
    max-width: 1024px;
    text-shadow: 1px 1px 1px rgb(65, 65, 65);
    z-index: 2;
    img {
        width: 40%;
        filter: drop-shadow(1px 1px 1px rgb(65 65 65));
    }
    span.text {
        font-weight: 700;
        color: white;
        font-size: 32px;
        margin-top: 7px;
        display: flex;
        p {
            color: #2c60e4;
            margin-left: 8px;
        }
    }
    span.desc {
        color: white;
        text-align: center;
        font-weight: 500;
        font-size: 19px;
        margin-top: 15px;
        line-height: 1.3;
    }
}

.center-logo {
    height: 10%;
    transform: none;
    transform: translate3d(0, 0, 1000px);
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    text-align: center;
}
.slides-title {
    position: absolute;
    bottom: 0;
    width: 100%;
    background: black;
    height: 40px;
    opacity: 0.8;
    font-size: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

.contact-button {
    --btnWidth: 150px;
    position: relative;
    overflow: hidden;
    font-size: 20px;
    padding: 15px;
    width: var(--btnWidth);
    height: 40px;
    border: 1px solid #2c60e4;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    margin-top: 50px;
    span {
        position: absolute;
        display: block;
        width: 0;
        height: 0;
        border-radius: 50%;
        background-color: #2c60e4;
        transition: 0.4s ease-in-out;
        transform: translate(-50%, -50%);
        z-index: 2;
    }

    p {
        color: #2c60e4;
        z-index: 3;
        position: absolute;
        transition: 0.5s ease-in-out;
    }
    &:hover {
        span {
            width: calc(var(--btnWidth) * 2.5);
            height: calc(var(--btnWidth) * 2.5);
        }
        p {
            color: white;
        }
    }
}

.btn-6:active {
    background-color: #af0d33;
}
.container-body {
    // max-width: 1024px;
    width: 100%;
    // height: 500px;
    // background: white;
    z-index: 2;
}
.swiper-area {
    margin-bottom: 50px;
    .swiper-title {
        color: white;
        font-size: 30px;
        margin-left: 20px;
        margin-bottom: 10px;
    }
}
.swiper {
    overflow: inherit !important;
    height: 400px;
    --hover-hight: 15px;

    .swiper-slide {
        position: relative;
        padding: 10px;
        box-sizing: border-box;
        cursor: pointer;

        &:hover {
            
            .swiper-slide-cont {
                margin-top: calc(-1 * var(--hover-hight));
                .bg-gradient {
                    .slide-cont-title {
                        bottom: var(--hover-hight);
                    }
                }
            }
        }

        .swiper-slide-cont {
            transition: 0.4s;
            margin-top: 0;
            border-radius: 10px;
            background-color: #af0d33;
            width: 100%;
            height: 100%;
            background-position: center;
            background-size: cover;
            overflow: hidden;
            display: block;

            img {
                width: 100%;
                height: 100%;
            }
            .bg-gradient {
                .slide-cont-title {
                    position: absolute;
                    bottom: 0;
                    color: white;
                    font-weight: 800;
                    box-sizing: border-box;
                    font-size: 21px;
                    padding: 11px;
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    transition : 0.4s;
                }
                position: relative;
                width: 100%;
                height: 100%;
                background: linear-gradient(to top, rgba(0, 0, 0, 0.8), transparent);
            }
        }
    }
}
.swiper.wide {
    .swiper-slide {
        height: 400px;
    }
}

.swiper.grid {
    height: 600px;

    .swiper-slide {
        height: calc((100% / 2) - 30px) !important;
    }
}
