$mobile: 767px;
$tablet: 1023px;
$desktop: 1024px;

/*반응형, 브라우저 크기가 767px 이하일때*/
@mixin mobile {
    @media (max-width: $mobile) {
        @content;
    }
}

/*반응형, 브라우저 크기가 768이상, 1023px 이하일때*/
@mixin tablet {
    @media (min-width: ($mobile+1)) and (max-width: $tablet) {
        @content;
    }
}

/*반응형, 브라우저 크기가 1024px 이상일때*/
@mixin desktop {
    @media (min-width: $desktop) {
        @content;
    }
}

#navbar {
    width: 100%;
    padding: 0 40px;
    box-sizing: border-box;
    height: 60px;
    display: flex;
    align-items: center;
    position: fixed;
    justify-content: center;
    z-index: 3;
}
#navbar-bg {
    background-color: white;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    opacity: 0.6;
}
#navbar-content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include desktop {
        max-width: 1024px;
    }
    @mixin tablet {
        max-width: 768px;
    }
}
#navbar-brand {
    display: flex;
}
#navbar-brand img {
    height: 30px;
}
#navbar-brand .text {
    font-size: 22px;
    font-weight: bold;
    color: rgb(46, 97, 230);
    position: relative;
    top: 3px;
    display: flex;
}
.nav-item {
  font-weight: 700;
  font-size: 1rem;
  text-decoration: none;
  color: rgb(46, 97, 230);
  margin-left: 40px;
}
